.skull-loader {
	width: 50px;
	height: 50px;
	margin-top: 20%;
	border: 4px dotted var(--pirate-gold);
	border-radius: 50%;
	animation: spin 1s infinite linear;
	z-index: 100 !important;
	cursor: none;
	user-select: none;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
